 .wrapper {
  width: 80%;
  margin: 15px;
  // border: 1px solid black;
  display: flex;
  flex-direction: column;
  align-items:center;
  h1 {
    margin: 1.8rem 1rem;
    @media(max-width: 650px) {
      margin: 1rem .5rem;
      font-size: 1.4rem;
      font-weight: 800;
    }
  }
  @media(max-width: 950px) {
    width: 100%;
    margin: 0 auto;
  }
   ol {
     display: flex;
     justify-content: center;
     flex-wrap: wrap;
     font-size: 1.5rem;
     margin: 0 1rem;
     padding: 0;
     @media(max-width: 950px){
       flex-direction: column;
       font-size: 1rem;
       margin: 0 .5rem;
     }
   }
   ol > li {
    margin: 1rem 1.4rem;
    color: red;
    span {
      display: inline-block;
      font-weight: 800;
      font-size: 1.5rem;
      color: black;
      border: 2px solid rgba($color: #5c5959, $alpha:.6);
      margin: 10px;
      padding: 2px 10px; 
      box-shadow: 10px -10px 20px rgba($color: #000000, $alpha:.9);
      // cursor: pointer;
      background-color: rgba($color: #e9e5e5, $alpha:.6);
      border-radius: 10px;
    }
    @media(max-width: 950px){
      margin: 10px 5px;
      span {
        font-weight: 500;
        font-size: .8rem;
        margin: 1px;
      }
    }
   }
}